.wmde-markdown {
  font-size: 16px;
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji;
  /* customized */
  word-break: break-word;
}
/* customized */
.blockquote-collapsed {
  .wmde-markdown > blockquote {
    max-height: 300px;
    overflow-y: scroll;
    padding-top: 10px;
    padding-bottom: 10px;
    box-shadow: rgb(240, 241, 242) 0px 2px 8px;
  }
}
.wmde-markdown > :first-child {
  margin-top: 0 !important;
}
.wmde-markdown > :last-child {
  margin-bottom: 0 !important;
}
.wmde-markdown code[class*='language-'],
.wmde-markdown pre[class*='language-'] {
  color: black;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}
.wmde-markdown pre code {
  background: none;
  overflow-wrap: normal;
  white-space: inherit;
  -moz-tab-size: 2;
  tab-size: 2;
  padding: 16px;
  font-size: 95%;
  line-height: 1.5;
  display: block;
  text-shadow: 0 1px #fff;
}
.wmde-markdown pre {
  margin-bottom: 18px;
  font-size: 85%;
  line-height: 1.45;
  position: relative;
  overflow-x: auto;
  background-color: #f6f8fa;
  border-radius: 3px;
}
.wmde-markdown code,
.wmde-markdown tt {
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
  font-size: 85%;
  margin: 0;
  padding: 0.2em 0.4em;
}
.wmde-markdown pre,
.wmde-markdown code,
.wmde-markdown tt {
  font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
}
.wmde-markdown img {
  max-width: 100%;
}
.wmde-markdown input {
  vertical-align: middle;
  margin: 0 0.2em 0.25em -1.6em;
}
.wmde-markdown h1,
.wmde-markdown h2 {
  border-bottom: 1px solid #eaecef;
  padding-bottom: 0.3em;
}
.wmde-markdown h1,
.wmde-markdown h2,
.wmde-markdown h3,
.wmde-markdown h4,
.wmde-markdown h5,
.wmde-markdown h6 {
  font-weight: 600;
  line-height: 1.25;
  margin-bottom: 16px;
  margin-top: 24px;
}
.wmde-markdown h1 {
  font-size: 2em;
}
.wmde-markdown h2 {
  font-size: 1.5em;
}
.wmde-markdown h3 {
  font-size: 1.25em;
}
.wmde-markdown h4 {
  font-size: 1em;
}
.wmde-markdown h5 {
  font-size: 0.875em;
}
.wmde-markdown h6 {
  font-size: 0.85em;
}
.wmde-markdown ol,
.wmde-markdown ul {
  padding-left: 2em;
}
.wmde-markdown ul {
  margin-bottom: 16px;
  margin-top: 0;
  list-style: initial;
}
.wmde-markdown > blockquote,
.wmde-markdown > blockquote blockquote {
  margin: 0;
  border-left: 0.25em solid #dfe2e5;
  color: #6a737d;
  padding: 0 1em;
}
.wmde-markdown > blockquote > :last-child,
.wmde-markdown > blockquote blockquote > :last-child {
  margin-bottom: 0;
}
.wmde-markdown > blockquote > :first-child,
.wmde-markdown > blockquote blockquote > :first-child {
  margin-top: 0;
}
.wmde-markdown hr {
  margin: 1.5em auto;
  border: 0;
  border-top: 2px dotted #eee;
  height: 1px;
}
.wmde-markdown > table,
.wmde-markdown > blockquote table {
  display: block;
  overflow: auto;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  font-size: 14px;
}
.wmde-markdown > table tr,
.wmde-markdown > blockquote table tr {
  background-color: #fff;
  border-top: 1px solid #c6cbd1;
}
.wmde-markdown > table td,
.wmde-markdown > blockquote table td,
.wmde-markdown > table th,
.wmde-markdown > blockquote table th {
  border: 1px solid #dfe2e5;
  padding: 6px 13px;
}
.wmde-markdown blockquote,
.wmde-markdown details,
.wmde-markdown dl,
.wmde-markdown ol,
.wmde-markdown p,
.wmde-markdown pre,
.wmde-markdown table,
.wmde-markdown ul {
  margin-bottom: 16px;
  margin-top: 0;
}
.wmde-markdown a {
  color: #0366d6;
  text-decoration: none;
}
.wmde-markdown a:hover {
  text-decoration: underline;
}
.wmde-markdown .namespace {
  opacity: 0.7;
}
.wmde-markdown .token.important {
  font-weight: normal;
}
.wmde-markdown .token.bold {
  font-weight: bold;
}
.wmde-markdown .token.italic {
  font-style: italic;
}
.wmde-markdown .token.entity {
  cursor: help;
}
.wmde-markdown-color .token.tag .attr-value {
  color: #032f62;
}
.wmde-markdown-color .token.property,
.wmde-markdown-color .token.tag,
.wmde-markdown-color .token.boolean,
.wmde-markdown-color .token.number,
.wmde-markdown-color .token.function-name,
.wmde-markdown-color .token.constant,
.wmde-markdown-color .token.symbol,
.wmde-markdown-color .token.deleted {
  color: #0060c9;
}
.wmde-markdown-color .token.punctuation {
  color: #a0a0a0;
}
.wmde-markdown-color code[class*='language-'] {
  color: black;
}
.wmde-markdown-color code[class*='language-'] .token.selector,
.wmde-markdown-color code[class*='language-'] .token.attr-name,
.wmde-markdown-color code[class*='language-'] .token.string,
.wmde-markdown-color code[class*='language-'] .token.char,
.wmde-markdown-color code[class*='language-'] .token.function,
.wmde-markdown-color code[class*='language-'] .token.builtin {
  color: #6f42c1;
}
.wmde-markdown-color code[class*='language-'] .token.inserted {
  color: #22863a;
  background-color: #f0fff4;
}
.wmde-markdown-color code[class*='language-'] .token.deleted {
  color: #b31d28;
  background-color: #ffeef0;
}
.wmde-markdown-color code[class*='language-'] .token.class-name {
  color: #6f42c1;
}
.wmde-markdown-color code[class*='language-'] .code-block {
  color: #032f62;
}
.wmde-markdown-color code[class*='language-'] .token.comment,
.wmde-markdown-color code[class*='language-'] .token.block-comment,
.wmde-markdown-color code[class*='language-'] .token.prolog,
.wmde-markdown-color code[class*='language-'] .token.doctype,
.wmde-markdown-color code[class*='language-'] .token.cdata {
  color: #7d8b99;
}
.wmde-markdown-color code[class*='language-'] .token.punctuation {
  color: #a0a0a0;
}
.wmde-markdown-color code[class*='language-'] .token.operator,
.wmde-markdown-color code[class*='language-'] .token.entity,
.wmde-markdown-color code[class*='language-'] .token.url,
.wmde-markdown-color code[class*='language-'] .token.variable {
  color: #d73a49;
  background: rgba(255, 255, 255, 0.5);
}
.wmde-markdown-color code[class*='language-'] .token.atrule,
.wmde-markdown-color code[class*='language-'] .token.attr-value {
  color: #004698;
}
.wmde-markdown-color code[class*='language-'] .token.keyword {
  color: #d63200;
}
.wmde-markdown-color code[class*='language-'] .token.regex,
.wmde-markdown-color code[class*='language-'] .token.important {
  color: #e90;
}
.wmde-markdown-color code[class*='language-'] .token.string {
  color: #0a53c1;
}
.w-md-editor-bar {
  position: absolute;
  cursor: s-resize;
  right: 0;
  margin-top: -11px;
  margin-right: 0;
  width: 14px;
  z-index: 3;
  height: 10px;
  border-radius: 0 0 3px 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.w-md-editor-bar svg {
  display: block;
  margin: 0 auto;
}
.w-md-editor-aree {
  overflow: auto;
  border-radius: 5px;
  padding: 10px;
}
.w-md-editor-text {
  min-height: 100%;
  position: relative;
  text-align: left;
  white-space: pre-wrap;
  word-break: keep-all;
  overflow-wrap: break-word;
  box-sizing: border-box;
  padding: 0;
  /* margin: 10px; */
  margin: 0 !important;
  font-size: 14px;
  line-height: 18px;
  -webkit-font-variant-ligatures: common-ligatures;
  font-variant-ligatures: common-ligatures;
}
.w-md-editor-text-pre,
.w-md-editor-text-input {
  margin: 0;
  border: 0;
  background: none;
  box-sizing: inherit;
  display: inherit;
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  -webkit-font-variant-ligatures: inherit;
  font-variant-ligatures: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  line-height: inherit;
  -moz-tab-size: inherit;
  tab-size: inherit;
  text-indent: inherit;
  text-rendering: inherit;
  text-transform: inherit;
  white-space: inherit;
  overflow-wrap: inherit;
  word-break: inherit;
  word-break: normal;
  padding: 0;
}
.w-md-editor-text-pre {
  position: relative;
  pointer-events: none;
}
.w-md-editor-text-input {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  resize: none;
  color: inherit;
  overflow: hidden;
  outline: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-text-fill-color: transparent;
}
.w-md-editor-text-input:empty {
  -webkit-text-fill-color: inherit !important;
}
/**
 * Hack to apply on some CSS on IE10 and IE11
 */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /**
    * IE doesn't support '-webkit-text-fill-color'
    * So we use 'color: transparent' to make the text transparent on IE
    * Unlike other browsers, it doesn't affect caret color in IE
    */
  .w-md-editor-text-input {
    color: transparent !important;
  }
  .w-md-editor-text-input::selection {
    background-color: #accef7 !important;
    color: transparent !important;
  }
}
.w-md-editor-text-pre {
  color: #333;
}
.w-md-editor-text-pre .table .punctuation {
  color: #c3c3c3;
}
.w-md-editor-text-pre .table .table-header {
  color: #000;
}
.w-md-editor-text-pre .url {
  color: #032f62;
}
.w-md-editor-text-pre .url .content {
  color: #0366d6;
  text-decoration: underline;
}
.w-md-editor-text-pre .hr {
  color: #999;
}
.w-md-editor-text-pre .blockquote {
  color: #a6a6a6;
}
.w-md-editor-text-pre .title,
.w-md-editor-text-pre .bold {
  color: #000;
}
.w-md-editor-toolbar {
  border-bottom: 1px solid #dfdfe0;
  background-color: #fbfbfb;
  padding: 4px 2px 4px 5px;
  border-radius: 3px 3px 0 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.w-md-editor-toolbar ul,
.w-md-editor-toolbar li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.w-md-editor-toolbar li {
  display: inline-block;
  font-size: 14px;
}
.w-md-editor-toolbar li button {
  border: none;
  height: 20px;
  line-height: 14px;
  background: none;
  color: #586069;
  padding: 4px;
  margin: 0 1px;
  border-radius: 3px;
  text-transform: none;
  font-weight: normal;
  overflow: visible;
  outline: none;
  cursor: pointer;
  transition: all 0.3s;
}
.w-md-editor-toolbar li button:hover {
  color: #06c;
  background-color: #e8e8e8;
}
.w-md-editor-toolbar li button:active {
  color: #6a57ff;
  background-color: #dadada;
}
.w-md-editor-toolbar li button:disabled {
  color: #ccc;
  cursor: not-allowed;
}
.w-md-editor-toolbar li button:disabled:hover {
  background-color: transparent;
  color: #ccc;
}
.w-md-editor-toolbar li.active button {
  color: #06c;
  background-color: #e8e8e8;
}
.w-md-editor-toolbar-divider {
  height: 14px;
  width: 1px;
  margin: -3px 3px 0 3px !important;
  vertical-align: middle;
  background-color: #ccc;
}
.w-md-editor {
  color: #24292e;
  text-align: left;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 0 0 rgba(16, 22, 26, 0),
    0 1px 1px rgba(16, 22, 26, 0.2);
  border-radius: 3px;
  position: relative;
  background-color: #fff;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
.w-md-editor-content {
  position: relative;
  border-radius: 0 0 3px 0;
}
.w-md-editor-input {
  width: 50%;
  height: 100%;
}
.w-md-editor-preview {
  width: 50%;
  box-sizing: border-box;
  box-shadow: inset 1px 0 0 0 #dfdfe0;
  position: absolute;
  padding: 10px 20px;
  overflow: auto;
  top: 0;
  right: 0;
  bottom: 0;
}
.w-md-editor-show-preview .w-md-editor-input {
  width: 0%;
  overflow: hidden;
  background-color: #fdfdfd;
}
.w-md-editor-show-preview .w-md-editor-preview {
  width: 100%;
  box-shadow: inset 0 0 0 0;
}
.w-md-editor-show-edit .w-md-editor-input {
  width: 100%;
}
.w-md-editor-show-edit .w-md-editor-preview {
  width: 0%;
  padding: 0;
}
.w-md-editor-fullscreen {
  overflow: hidden;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.w-md-editor-fullscreen .w-md-editor-content {
  height: 100%;
}
.w-md-editor .language-javascript .punctuation {
  color: #000;
}
.w-md-editor .language-javascript .function {
  color: #6f42c1;
}
.w-md-editor .language-javascript .string {
  color: #032f62;
}
.w-md-editor .language-javascript .keyword {
  color: #d73a49;
}
